*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  width: 220px;
  pointer-events: none;
  border: 1px solid green;
  box-shadow: 1px 2px  5px rgb(21, 83, 21);
  margin-bottom: 35px;
}
p{margin-top: 20px;}
.div-form{
  margin-top: 10px;
  max-width: 300px;
}
.form{
  width: 100%;
}
.form input, textarea{
  width: 100%;
  margin-bottom: 5px;
  outline: 0;
  padding: 3px;
}
.form input{
  height: 30px;
}
.form textarea{
  height: 80px;
}
.form button{
  width: 100%;
  height: 30px;
  outline: 0;
  border: 0;
  background-color: rgb(1, 102, 1);
  color: white;
  font-weight: bold;
  cursor: pointer;
}
h2{
  margin-top: 40px;
  font-size: 16px;
  font-weight: normal;
  color: green;
}
.Enviando{
  animation: alternate-reverse efeito 1.5s infinite;
}
@keyframes efeito{
  100%{background-color: green;}
  0%{background-color: rgb(119, 202, 119);}
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
